<template>
	<dir>
		<div>
			<ImageBarcodeReader
				@decode="onDecode"
				@error="onError"
			></ImageBarcodeReader>
		</div>
		<div>
			<v-quagga
				:onDetected="logIt"
				:readerSize="readerSize"
				:readerTypes="['ean_reader']"
			></v-quagga>
		</div>
	</dir>
</template>

<script>
	import Vue from 'vue';
	import VueQuagga from 'vue-quaggajs';
	Vue.use(VueQuagga);
	import { ImageBarcodeReader } from 'vue-barcode-reader';
	export default {
		name: 'barcodeEntry',
		data() {
			return {
				readerSize: {
					width: 640,
					height: 480,
				},
				detecteds: [],
			};
		},
		components: {
			ImageBarcodeReader,
		},
		methods: {
			logIt(data) {
				alert('detected', data);
			},
			onDecode(result) {
				alert(result);
			},
			onError(result) {
				alert(result);
			},
		},
	};
</script>
